<template>
  <div class="base">
    <div class="column">
      <h3 class="my-team">My Picks</h3>
      <div class="picks-box">
        <player-overview
          v-for="player in myPicks"
          :isMaster="masterIds.includes(player.name)"
          :key="player.name"
          :player="player"/>
      </div>
    </div>
    <div class="column">
      <h1 class="players">Players</h1>
      <div class="players-box">
        <player-overview
          v-for="player in poolPlayers"
          @picked="$emit('picked', $event)"
          :player="player"
          :skillRange="player.skillRange"
          :key="player.name"/>
      </div>
    </div>
    <div class="column">
      <h3 class="players">Opponent Picks</h3>
      <div class="picks-box">
        <player-overview
          v-for="player in opponentPicks"
          :isMaster="masterIds.includes(player.name)"
          :key="player.name"
          :player="player"/>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "MasterPicks",
  props: {
    masterIds: {
      type: Array,
      default: () => ([])
    },
    myPicks: {
      type: Array,
      default: () => ([])
    },
    opponentPicks: {
      type: Array,
      default: () => ([])
    },
    poolPlayers: {
      type: Array,
      default: () => ([])
    }
  }
}
</script>

<style>
  .base {
    display: flex;
    margin: 20px 60px; 
  }

  .base > .column {
    flex: 1;
  }

  .picks-box {
    border: 3px solid black;
    padding: 20px 0;
    min-height: 308px;
  }

  .players-box {
    padding-top: 20px;
  }
</style>